const uni = require('uni').default;














import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'makePhoneCall',
			disabled: true
		}
	},
	methods: {
		bindInput: function (e) {
			this.inputValue = e.target.value
			if (this.inputValue.length > 0) {
				this.disabled = false
			} else {
				this.disabled = true
			}
		},
		makePhoneCall: function () {
			uni.makePhoneCall({
				phoneNumber: this.inputValue,
				success: () => {
					console.log("成功拨打电话")
				}
			})
		}
	},
	components: {
		pageHead

	}
}



